.footer {
  color: #91a0b6;
  display: flex;
  flex-direction: row;
  font-size: 14px;
  font-weight: 400;
  justify-content: space-between;
  line-height: 20px;
  padding: 24px 183px;


  &__anotation {
    display: flex;
    flex-direction: row;
  }
}

@media screen and (max-width: 1280px) {
  .footer {
    padding: 0;
  }
}
