.new {
  margin-bottom: 56px;

  &__name {
    font-size: 14px;
    font-weight: 600;
    padding: 12px;
    width: 78px;
    background: #ed685c;
    color: #fff;
    border-radius: 4px;
    margin-bottom: 16px;
    color: #fff;
    padding-right: 0;
    clip-path: polygon(103% 0, 92% 50%, 103% 100%, 0% 100%, 0 50%, 0% 0%);
  }

  &__block {
    display: flex;
    flex-direction: row;
    gap: 32px;
  }

  &__right {
    width: 521px;
  }

  &__date {
    font-weight: 400;
    color: #adb8c8;
  }

  &__heading {
    font-weight: 700;
    font-size: 48px;
    line-height: 72px;
    color: #242424;
  }

  &__text {
    font-weight: 400;
    color: #adb8c8;
  }
}

@media screen and (max-width: 1100px) {
  .new {
    &__block {
      display: flex;
      flex-direction: column;
    }

    &__left {
      text-align: center;
    }

    &__right {
      margin: 0 auto;
      text-align: center;
    }
  }
}
